$font-light: white;
$font-stack: 'Raleway', sans-serif;
$color-tp: #0075b0;


:root {
  --easing: cubic-bezier(0.5, 0.7, 0.4, 1);
}

//Accordian
/* Style the buttons that are used to open and close the accordion panel */
.accordian-panel {
  font-size: 1rem;
  color: #5fa5d3;

  padding: 1rem 5vw 1rem;


}

.a_window {

  p {
    font-weight: 600;
    margin-bottom: .4rem;
    padding: .3rem;
  }
}

.accordion {
  display: inline;
  padding-left: 3vw;
  font-size: 1rem;
  //width: 3rem;
  //height: 3rem;
  cursor: pointer;
  //border: 10px solid black;
  opacity: 60%;
  //border-radius: 50%;
  transition: 0.4s;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .accordion:hover {
}

/* Style the accordion panel. Note: hidden by default */
.accordian_window {
  padding: 0 18px;
  display: none;
  overflow: hidden;
}

//

article {
  width: 100%;
  color: $font-light;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;

  a {
    font-size: 2.22vw;
    padding: 24px 2vw;
    display: inline-block;
    text-align: center;
    color: $font-light;
    border: 3px solid $font-light;
    border-radius: 10px;
    text-decoration: none;
  }

}

.intro-p {
  position: absolute;
  width: 65vw;

  p {
    font-weight: 400;
    font-size: .8rem;
    line-height: 1.6;
    margin-bottom: 1.2rem;
    color: #3DB6B8;

  }
}

.header {
  height: 105vh;
  width: 100%;
  position: relative;

  .header_text {
    position: absolute;
    top: 10vh;
    left: 6vw;
    color: #3DB6B8;

    h1 {
      font-size: 2.8rem;
      margin-bottom: 1vh;
      line-height: 1.4;
      color: #3DB6B8;
    }


    .header_image {
      margin-top: 1.5rem;
      grid-area: image;
      width: 100%;
      height: 45vh;
      top: 50vh;
      overflow: hidden;
      position: relative;
      opacity: 80%;
      z-index: 1; /* fix for Safari */

      img {
        left: 30vw;
        width: 100%;
        position: absolute;
        object-fit: contain;
        object-position: 50% 50%;
        height: 130%;
        transform: rotate(320deg);
      }
    }

  }

}

.content {
  transition: all 0.3s ease-out;
  opacity: 1;
  transform: translateY(0);
}

.agency {
  margin: 0;
  min-height: 75vh;
  padding: 0 0 3vh;
  flex-direction: column;
  font-family: $font-stack;
  background-color: #fff;

  p {
    font-size: .8rem;
    line-height: 1.5rem;
    font-weight: 400;
    padding-left: 3vw;
    margin: .3rem 0;


  }
}

.agency_text {
  padding: 0 5vw;
}

.agency_name {
  grid-area: name;
  font-size: 1.1rem;
  font-weight: 400;
  padding: 5vh 5vw 0;

  h3 {
    font-size: 2.3rem;
  }
}

.agency_tools {
  grid-area: tools;
  font-size: 1.1rem;
  font-weight: 600;
  padding: 5vw;
}

.agency_button {
  grid-area: button;
  font-size: 1.2rem;
  font-weight: 600;
  border-radius: 10px;
  padding: 1rem 2rem;
  align-self: center;
  width: 35vw;
  display: flex;
  justify-content: center;
  align-items: center;

  a {
    text-decoration: none;
  }
}

.agency_sample {
  width: 90vw;
  overflow: hidden;
  position: relative;
  z-index: 1; /* fix for Safari */

  img {
    left: 0;
    width: 100%;
    position: absolute;
    object-fit: contain;


  }
}

