/*  Travelport portfolio page  */

.shadow {

  &.all {
    box-shadow: 3px 3px 5px 5px #bbb;
  }

  &.top {
    box-shadow: 0px -15px 10px -15px #111;
  }

  &.bottom {
    box-shadow: 0px 15px 10px -15px #111;
  }

  &.left {
    box-shadow: -15px 0px 10px -15px #111;
  }

  &.right {
    box-shadow: 15px 0px 10px -15px #111;
  }
}

.panel {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 1.5em;

}

.horizontal {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  position: fixed;
  height: 100vh;
}

.scroll {
  overflow-x: hidden;
}

.fixed_port {
  position: absolute;
  top: 3rem;
  left: 3rem;
  font-size: 3rem;
  z-index: 10;
  color: white;
}

.portfolio_name {
  width: 100%;
  height: 25vh;
  color: white;

  h4 {
    padding: 17vh 0 0 5vw;
    font-size: 3rem;
    font-weight: 400;
    z-index: 8;

  }
}

.portfolio_image {
  grid-area: image;
  position: relative;

  z-index: 1; /* fix for Safari */

  img {
    left: 0;
    top: 0;
    width: 100%;
    position: absolute;

  }
}

.portfolio_text {
  margin-top: 5rem;
  color: $color-tp;
  font-size: 2.3rem;
  font-weight: 600;
}

#tp {

  .portfolio_name {
    width: 100%;
    background-color: $color-tp;
  }

  .redefining {

    .portfolio_image {
      height: 75vh;
      width: 95%;
      padding-bottom: 10vh;
      top: 5vh;

      img {
        object-fit: contain;
